$bg1: #f6d365;
$bg2: #fda085;
$header_color: white;
$input_color: white;
$accent: #ff6f47;
$font_color: #333;
$font_color_completed: #777;
$checkbox: rgb(11, 212, 162);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    background-image: linear-gradient(120deg, $bg1 0%, $bg2 100%);
    color: $header_color;
    font-family: 'Poppins', sans-serif;
    min-height: 100vh;
}
header {
    font-size: 5rem;
    color: $header_color;
    font-weight: 700;
    width: 100%;
    position: relative;
    text-align: center;

    i {
        margin-left: 20px;
        color: $font_color_completed;
        font-size: 2rem;
        opacity: 0;
        transition: all .3s ease;
    }
    &:hover {
        i {
            opacity: 1;
        }
    }
}
header, form {
    min-height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
form input, form button {
    padding: .5rem;
    font-size: 2rem;
    border: none;
    outline: none;
    background: $input_color;
}
form input {
    color: $font_color;
    border-radius: 10px 0 0 10px;
    border: 3px solid transparent;
    transition: all .3s ease;

    &:focus, &:hover {
        border: 3px solid $accent;
    }
}
form button {
    color: $accent;
    cursor: pointer;
    background: $input_color;
    border-radius: 0 10px 10px 0;
    border: 3px solid transparent;
    transition: all .3s ease;

    &:hover {
        background: $accent;
        border: 3px solid $accent;
        color: $input_color;
    }
}
.filter-todo {
    padding: 1rem;
}
.todo-container {
    display: flex;
    justify-content: center;
    align-items: center;

    .todo-list {
        min-width: 30%;
        list-style: none;

        .todo {
            margin: .5rem;
            background: $input_color;
            font-size: 1.5rem;
            color: $font_color;
            display: flex;
            justify-content: space-between;
            align-items: center;
            transition: all 1s ease;
            border-radius: 10px;
            overflow: hidden;

            li {
                flex: 1;
                padding: 0 .5rem;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;

                &.completed {
                    text-decoration: line-through;
                    color: $font_color_completed;
                }
            }
            .trash-btn, .complete-btn {
                color: $input_color;
                cursor: pointer;
                background: $accent;
                outline: none;
                padding: 1rem;
                font-size: 1rem;
                border: 3px solid $accent;
                transition: all .3s ease;
            
                &:hover {
                    background: $input_color;
                    color: $accent;
                }
                .fa-trash, .fa-check {
                    pointer-events: none;
                }
            }
            .trash-btn {
                border-radius: 0 10px 10px 0;
            }
            .complete-btn {
                background: $checkbox;
                border: 3px solid $checkbox;

                &:hover {
                    background: $input_color;
                    color: $checkbox;
                }
            }
        }
    }
}
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: none;
    box-shadow: none;
    border: 0 !important;
    background-image: none;
}
.select {
    border-radius: 10px;
    margin: 1rem;
    position: relative;
    overflow: hidden;

    &:after {
        content: "\25BC";
        position: absolute;
        top: 0;
        right: 0;
        padding: 1rem;
        color: $input_color;
        background: $accent;
        cursor: pointer;
        pointer-events: none;
    }
    select {
        color: $accent;
        background: $input_color;
        border: 3px solid transparent;
        font-family: 'Poppins', sans-serif;
        outline: none;
        cursor: pointer;
        width: 12rem;
        transition: all .3s ease;

        &:hover {
            background: $accent;
            color: $input_color;
            border: 3px solid $accent;
        }
        option {
            background: $input_color;
            color: $accent;
            border: none;
            outline: none;
        }
    }
}
#stfDialogPlace > div > div > div > div {
    border-radius: 20px;

    > div {
        color: $font_color;
        background: $input_color;

        input {
            padding: .35rem;
            font-size: 2rem;
            outline: none;
            background: $input_color;
            color: $font_color;
            border-radius: 10px;
            border: 3px solid transparent;
            transition: all .3s ease;

            &:focus, &:hover {
                border: 3px solid $accent;
            }
        }
        button {
            outline: none;
            border-radius: 10px;
            transition: all .3s ease;

            &:nth-child(1) {
                background: $accent;
                color: $input_color;
                border: 3px solid $accent;

                &:hover {
                    background: $input_color;
                    color: $accent;
                }
            }
            &:nth-child(2) {
                background: $input_color;
                color: $font_color;
                border: 3px solid transparent;

                &:hover {
                    background: $input_color;
                    color: $accent;
                    border: 3px solid $accent;
                }
            }
        }
    }
}
.fab-container {
    max-width: 50%;
    max-height: 50%;
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 20px;

    .floating-btn {
        border-radius: 50%;
        border: 3px solid transparent;
        width: 50px;
        height: 50px;
        background: $accent;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $input_color;
        cursor: pointer;
        transition: all .3s ease;

        i {
            font-size: 30px;
        }
        &:hover {
            border: 3px solid $accent;
            color: $accent;
            background: $input_color;
        }
    }
    .floating-text {
        font-size: 17px;
        margin-top: 10px;
        color: $input_color;
        text-shadow: 0 0 5px rgba($color: black, $alpha: .3);
        border-radius: 15px;
        padding: 2px 10px;
        background: rgba($color: white, $alpha: .1);
        box-shadow: 0 0 15px rgba($color: black, $alpha: .2);
        backdrop-filter: blur(10px);
        transition: all .3s ease;

        i {
            color: $checkbox;
            margin-right: 15px;
        }
    }
}

@media (max-width: 700px) {
    header {
        font-size: 10vw;

        i {
            font-size: 5vw;
        }
    }
    header, form {
        width: 100%;
        min-height: 20vh;
        padding: 0 10px;

        input {
            width: 70%;
            position: absolute;
            top: 20vh;
            left: 5%;
        }
        button {
            width: 20%;
            position: absolute;
            top: 20vh;
            right: 5%;
        }
    }
    .todo-list {
        width: 95%;
    }
    .select {
        position: absolute;
        top: 27vh;
    }
    .fab-container {
        max-width: 100%;

        .floating-text {
            width: 100%;
        }
    }
}
